.products {
	max-width: 1406px;
	margin: auto;
	margin-bottom: 100px;
	display: flex;
	flex-wrap: wrap;
	gap: 24px;
	padding: 0 30px;
}

.products__productcard {
	width: 250px;
	height: 358px;
	background-color: #fff;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
	border-radius: 16px;
	display: flex;
	flex-direction: column;
	overflow: hidden;
}

.productcard__image {
	width: 100%;
	height: 70%;
}

.productcard__image img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.productcard__info {
	padding: 16px;
}

.productcard__info__name {
	font-weight: 400;
	margin-bottom: 4px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	cursor: help;
}

.productcard__info__rating {
	margin-bottom: 16px;
	position: relative;
	width: 76px;
}

.stars_empty {
	width: 76px;
	height: 16px;
	fill: white;
}

.stars-filled__container {
	position: absolute;
	top: 0;
	left: 0;
	overflow: hidden;
	width: 50%;
}

.stars_filled {
	width: 76px;
	height: 16px;
	fill: var(--yellow);
}

.productcard__info__price {
	font-size: 22px;
	font-weight: 800;
}