@import url("./variables.css");

@font-face {
	font-family: "Circe bold";
	src: url("./fonts/CirceRounded-AltBold.woff");
}

@font-face {
	font-family: "Circe Extrabold";
	src: url("./fonts/CirceRoundedAlt-Regular2.woff");
}

body {
	font-family: "Nunito", Arial, Helvetica, sans-serif;
	font-weight: 700;
	background-color: var(--body-bg);
	color: var(--black);
}

.App {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}

.main {
	flex: 1 1 auto;
	max-width: 1920px;
	width: 100%;
	margin: auto;
}